import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        isUserLoggedIn: false,
    },
    mutations: {
        SET_USER_LOGGED_IN(state, payload){
            state.setUserLoggedIn = payload;
        }
    },
    actions: {
        setUserLoggedIn({commit}, loggedIn){
            console.log(commit, loggedIn);
            commit("SET_USER_LOGGED_IN")
        }
    },
    getters: {

    },
});