<template>
        <div id="example">
            <h2 class="uppercase text-yellow-500 font-semibold text-lg mt-20 ml-2">Upcoming Movies</h2>
            <carousel-3d 
            :key="upcomingMovies.length"
            :controls-visible="true" 
            :clickable="false" 
            :listData="upcomingMovies"
            :height="500">
                <slide :index="i" :key="i" v-for="(movie, i) in upcomingMovies">
                <figure>
                    <img :src="posterImage(movie)">
                    <figcaption>
                        <router-link :to="`/movie/${movie.id}`">
                            {{ movie.title }}
                        </router-link>
                    </figcaption>
                </figure>
                </slide>
            </carousel-3d>
        </div>
</template>

<script>
import { Carousel3d, Slide } from 'vue-carousel-3d'

export default {
    components:{
        Carousel3d,
        Slide,
    },
    mounted(){
        this.getUpcomingMovies();
    },
    data(){
        return {
            upcomingMovies : [],
        }
    },
    computed:{
        
    },
    methods :{
        async getUpcomingMovies(){
             try{
                const response = await this.$http.get("/movie/upcoming?&api_key=4a8a8d89f3e4792a5aa5377b9fb43f4e");
                this.upcomingMovies = response.data.results.slice(1, 6);
                // const date = this.getCurrentDate();
                console.log('dates :' + response.data.results.slice(1,6));
                // console.log('dates :' + response.data.results.filter((x) => x.release_date >= date).slice(1,6));

            }
            catch(error){
                console.log(error);
            }
        },
         getCurrentDate() {
                    const today = new Date();
                    const date = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate();
                    // const time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
                    // const dateTime = date +' '+ time;
                  return  date;
        },
        posterImage(movie){
            if(movie.poster_path){
                return "https://image.tmdb.org/t/p/w500" + movie.poster_path;
            }
            return "https://placehold.it/360x270";
        }
    }
}
</script>

<style scoped>
.carousel-3d-container figure {
  margin:0;
}

.carousel-3d-container figcaption {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  bottom: 0;
  position: absolute;
  bottom: 0;
  padding: 15px;
  font-size: 12px;
  min-width: 100%;
  box-sizing: border-box;
}
</style>