<template>
    <div class="mx-5">
        <h2 class="uppercase mt-5 text-yellow-500 text-lg font-semibold">
            Popular Movies
        </h2>
        <div class="grid grid-cols-1 sm:grid-cols-3 md:grid-cols-3 lg:grid-cols-4 gap-8">
             <MovieItem :key="movie.id" v-for="movie in movies" :movie="movie" :genres="genres"/>
        </div>

         <!-- <div class="flex justify-center">
            <div class="spinner"></div>
        </div> -->
        
    </div>
</template>

<script>

let currentPage = 1;

import MovieItem from "../items/MovieItem"

export default {
    data (){
        return {
            movies: [],
            genres: [],
        }
    },
    components :{
        MovieItem,
    },
    mounted(){
        this.getGenre();
        this.getPopular(currentPage);
        // this.scroll();
    },
    methods: {
        async getPopular(page){
            try{
                const response = await this.$http.get("/movie/popular?page="+ page + "&api_key=4a8a8d89f3e4792a5aa5377b9fb43f4e")
                // this.movies = response.data.results;
                this.movies.push(... response.data.results);
            }
            catch(error){
                console.log(error);
            }
        },
        async getGenre(){
             try{
                const response = await this.$http.get("/genre/movie/list?api_key=4a8a8d89f3e4792a5aa5377b9fb43f4e")
                this.genres = response.data.genres;
            }
            catch(error){
                console.log(error);
            }
        },
         scroll() {
            window.onscroll = () => {
                let bottomOfWindow =
                document.documentElement.scrollTop + window.innerHeight ===
                document.documentElement.offsetHeight;

                if (bottomOfWindow) {
                    currentPage += 1;
                    this.getPopular((currentPage += 1));
                }
            };
        },
       
    }
}
</script>