<template>
    <div class="container mx-auto px-4 py-5">
        <h2 class="text-yellow-500 text-lg font-semibold">
            POPULAR ACTORS
        </h2>

        <div class="grid grid-cols-1 sm:grid-cols-3 md:grid-cols-5 lg:grid-cols-5 gap-8">
            <ActorsItem :key="actor.id" v-for="actor in this.actors" :actor="actor" />
        </div>

        <div class="text-center mt-5">
            <a href="" v-on:click.prevent="Previous()">Previous</a>
            <a href="" v-on:click.prevent="Next()" class="ml-5">Next</a>
        </div>
        
        <!-- <div class="flex justify-center">
            <div class="spinner"></div>
        </div> -->
    </div>
</template>

<script>

let currentPage = 1;

import ActorsItem from "../items/ActorsItem"
export default {
    components:{
        ActorsItem,
    },
    data(){
        return {
            actors: [],
        }
    },
    mounted(){
         this.getActors(currentPage);
        //  this.scroll();
    },
    methods: {
        async getActors(page){
            try{
                const response = await this.$http.get("/person/popular?page=" + page + "&api_key=4a8a8d89f3e4792a5aa5377b9fb43f4e")
                // this.actors.push(... response.data.results);
                this.actors = response.data.results;
            }
            catch(error){
                console.log(error);
            }
        },
        scroll() {
            window.onscroll = () => {
                let bottomOfWindow =
                document.documentElement.scrollTop + window.innerHeight ===
                document.documentElement.offsetHeight;

                if (bottomOfWindow) {
                    currentPage += 1;
                    this.getActors((currentPage += 1));
                }
            };
        },
        Next(){
            currentPage += 1;
            this.getActors(currentPage);
        },
        Previous(){
            currentPage -= 1;
            this.getActors(currentPage);
        }
    },
}
</script>