<template>
    <div class="mt-5 flex relative">
        <input type="text" 
        class="rounded-full bg-gray-600 px-7 w-50 h-10 mr-3 focus:outline-none focus:outlne-shadow" 
        placeholder="Search.."
        @input="debounceSearch"
        v-model="searchTerm"
        ref="searchBox"
        @focus="handleFocus"
         />
        <div class="absolute top-0 or-3">
      <svg
        class="fill-current w-4 text-gray-300 mt-2 ml-2 mt-3"
        viewBox="0 0 24 24"
      >
        <path
          class="heroicon-ui"
          d="M16.32 14.9l5.39 5.4a1 1 0 01-1.42 1.4l-5.38-5.38a8 8 0 111.41-1.41zM10 16a6 6 0 100-12 6 6 0 000 12z"
        />
      </svg>
    </div>
    <div class="absolute mt-10 ml-2 rounded bg-gray-600 w-60 z-50" >
       <ul class="mt-3" v-if="showSearchResult">
           <li v-for="(movie,index) in searchResult" :key="index" class="flex items-center border-b border-gray-500 p-1">
                   <img :src="posterPath(movie.poster_path)" alt="" class="w-10" />
                     <router-link :to="`/movie/${movie.id}`" @click.native="showSearchResult = false">
                        <span class="ml-3">{{ movie.original_title}}</span>
                    </router-link>
           </li>
       </ul>
       <ul class="px-3" v-if="searchResult.length == 0 && showSearchResult">
         <li> 3">No result found for "{{ searchTerm }}"</li>
       </ul>
    </div>
       <!-- <AccountDropdown /> -->
    </div>
</template>

<script>
// import AccountDropdown from './AccountDropdown'
export default {
  components :{
    // AccountDropdown,
  },
  data(){
    return {
        searchResult: [],
        searchTerm: '',
        showSearchResult : false,
    }
  },
  mounted(){
     this.keyboardEvents();
  },
  methods: {
    debounceSearch(event){
      clearTimeout(this.debounce);
      this.debounce = setTimeout(() => {
        if(event.target.value.length > 2){
            this.fetchSearch(event.target.value);
        }
        else {
          this.showSearchResult = false;
        }
      }, 600)
    },
   async fetchSearch(keyword){
      try {
        const response = await this.$http.get("/search/movie?api_key=4a8a8d89f3e4792a5aa5377b9fb43f4e&query=" + keyword);
        this.searchResult = response.data.results;
        this.showSearchResult = (response.data.results) ? true : false;
        console.log(this.searchResult);
      } catch (error) {
        console.log(error);
      }
    },
     posterPath(poster_path){
       if(poster_path){
         return "https://image.tmdb.org/t/p/w500/" + poster_path;
       }
       else{
         return "https://via.placeholder.com/50x75";
       }
        
    },
    keyboardEvents(){
      let windowObj = this;

      window.addEventListener("click", (e) => {
        if(!this.$el.contains(e.target)){
           this.showSearchResult = false;
        }
      });
      window.addEventListener("keypress", (e) => {
        //for slash /
          if(e.keyCode == "47"){
             e.preventDefault();
              windowObj.$refs.searhBox.focus();
          }
      });

      window.addEventListener("keydown", (e) => {
          if(e.key == "Escape"){
              this.showSearchResult = false;
          }
      });
    },
    handleFocus(){
      if(this.searchTerm != ""){
        this.showSearchResult = true;
      }
    },
  }
}
</script>