<template>
    <div class="container mx-auto border-b border-gray-600 px-4 py-4">
        <h2 class="text-4xl font-semibold mb-5">
            Cast
        </h2>
        <div class="grid grid-cols-1 sm:grid-cols-3 md:grid-cols-5">
            <div class="mr-2 mt-5" v-for="(cast, index) in casts" :key="index">
               <router-link :to="`/actor/${cast.id}`">
                    <img :src="castProfileImage(cast)" alt="" class="hover:opacity-60 transition easy-in-out" />
                 <span class="text-white-300 text-sm">{{ cast.name }}  </span>
                 <div>
                     <span class="text-gray-400 text-sm">{{ cast.character }} </span>
                 </div>
               </router-link>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        casts:{
            required: true,
        }
    },
    methods:{
        castProfileImage(cast){
            if(cast.profile_path){
                return "https://image.tmdb.org/t/p/w200" + cast.profile_path;
            }
            else{
                 return "https://via.placeholder.com/200x300";
            }
        }
    }
}
</script>